<template>
  <b-container>
    <b-row>
      <b-col sm="12" class="px-0" v-if="total_articles.length && !show">
        <BreadCrumbs v-bind="{ ...total_articles[0] }" :class="$route.params && $route.params.c_id ? 'block' : 'd-none'" />
        <h2 class="text-capitalize header-body pt-0" v-if="total_articles.length && $route.params && $route.params.c_id">{{ total_articles[0].section_name }}</h2>
        <div class="mb-1" v-for="article in total_articles" :key="article.id">
          <span class="fe fe-chevron-right mr-2"></span>
          <b-link @click="showArticle(article.name, article.id)">{{ article.name.trim() }}</b-link>
        </div>
      </b-col>

      <b-col
        sm="12"
        :class="$route.params && $route.params.c_id && $route.params.s_id ? 'align-items-center d-flex justify-content-center px-0 vh-100' : ''"
        v-else-if="!total_articles.length && !show"
      >
        No Articles Found
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
  import Articles from "@/js/support/cname/articles";
  import BreadCrumbs from "./BreadCrumbs";
  import { bus } from "@/js/core/emit-bus";
  export default {
    data() {
      return {
        total_articles: "",
        show: true,
      };
    },
    props: {
      category_id: Number,
    },
    components: { BreadCrumbs },
    methods: {
      // Navigate to selected article
      showArticle(name, article_id) {
        bus.$emit("close_overlay", true);
        // eslint-disable-next-line no-useless-escape
        let regex = /[`~!@#$%^&*()_|+\-=?;:'",.<>\{\}\[\]\\\/]/gm;
        let article_name = name.replaceAll(regex, "");
        let params = {
          article_name: article_name
            .trim()
            .toLowerCase()
            .replaceAll(" ", "-"),
          a_id: article_id,
          c_id: this.$route.params.c_id || this.category_id,
          s_id: this.$route.params.s_id,
        };
        if (!this.$route.params?.s_id) delete params.s_id;
        this.$router.push({
          name: "Articles",
          params,
          query: { type: "widget" },
        });
      },

      // Get total articles corresponding to selected category & section
      async getArticles(category_id, section_id) {
        try {
          this.total_articles = await Articles.get_Articles(category_id, section_id);
          this.show = false;
          bus.$emit("close_overlay", false);
        } catch (error) {
          this.show = false;
        }
      },
    },
    async mounted() {
      await this.getArticles(this.$route.params.c_id || this.category_id, this.$route.params.s_id);
    },
  };
</script>
<style>
  .custom-breadcrumb .breadcrumb-item + .breadcrumb-item::before {
    background: #6e84a3;
    margin-top: 6px;
    height: 12px;
  }
  .custom-breadcrumb .breadcrumb-item.active {
    color: #6e84a3 !important;
  }
  .bg-custom-black {
    background-color: #2a2c2d !important;
  }
</style>
