import Data from "@/js/core/data";

const Articles_Server = "https://api.${region}.500apps.com/support/kb/${window.domain}/${window.user}/kb_details";

class Articles extends Data {
  constructor() {
    super(Articles_Server);
    this.disablePCors();
  }

  // Get Articles
  async get_Articles(category_id, section_id) {
    let url = Articles_Server;
    this.params = section_id ? `category_id=${category_id}&section_id=${section_id}` : `category_id=${category_id}`;
    this.url = eval("`" + url + "`");
    return await super.get();
  }
}
export default new Articles();
