import Data from "@/js/core/data";

const Sections_Server = "https://api.${region}.500apps.com/support/kb/${window.domain}/${window.user}/sections";
class Sections extends Data {
  constructor() {
    super(Sections_Server);
    this.disablePCors();
  }

  // Get Sections
  async get(category_id) {
    let url = Sections_Server;
    this.params = `category_id=${category_id}`;
    this.url = eval("`" + url + "`");

    return await super.get();
  }
}
export default new Sections();
