<template>
  <div>
    <div v-if="sections.length" class="d-inline-flex flex-column">
      <div class="mb-2 " v-for="section in sections" :key="section.id" @click="showArticles(category_id, section.id, section.name)">
        <span class="fe fe-chevron-right mr-2"></span>
        <b-link
          >{{ section.name.trim() }} <span v-if="!$route.params.c_id">({{ getArticleCount(section.name.trim()) }})</span></b-link
        >
      </div>
    </div>
    <Articles v-else v-bind="{ category_id }" />
  </div>
</template>

<script>
  import Sections from "@/js/support/cname/sections";
  import { bus } from "@/js/core/emit-bus";
  import Articles from "./Articles.vue";

  export default {
    components: { Articles },
    data() {
      return {
        sections: [],
      };
    },
    props: {
      category_id: Number,
      sections_count: {
        type: Array,
        default: () => [],
      },
    },
    methods: {
      // Get All sections list under a category
      async getSections(category_id) {
        this.sections = await Sections.get(category_id);
        bus.$emit("close_overlay", false);
      },

      showArticles(category_id, section_id, name) {
        bus.$emit("close_overlay", true);
        // eslint-disable-next-line no-useless-escape
        let regex = /[`~!@#$%^&*()_|+\-=?;:'",.<>\{\}\[\]\\\/]/gm;
        let section_name = name.replaceAll(regex, "");
        this.$router.push({
          name: "Sections",
          params: {
            section_name: section_name
              .trim()
              .toLowerCase()
              .replaceAll(" ", "-"),
            s_id: section_id,
            c_id: category_id,
          },
          query: { type: "widget" },
        });
      },
      getArticleCount(section_name) {
        let article_count = this.sections_count.find((section) => section.name?.trim() === section_name);
        return article_count ? article_count.count : 0;
      },
    },
    async mounted() {
      await this.getSections(this.category_id);
    },
  };
</script>
