<template>
  <div class="mt-6 pt-4 mb-4">
    <b-breadcrumb class="align-items-end d-flex pt-0 custom-breadcrumb">
      <b-breadcrumb-item class="pl-0" @click="goToHome"> Home</b-breadcrumb-item>
      <b-breadcrumb-item :active="$route.name == 'Categories'" @click="goToCategory">
        <span class="text-capitalize">{{ $route.params.category_name ? $route.params.category_name.replaceAll("-", " ") : category_name }} </span>
      </b-breadcrumb-item>
      <b-breadcrumb-item :active="$route.name == 'Sections'" v-if="$route.name != 'Categories' && $route.params.s_id" @click="goToSection">
        <span class="text-capitalize"> {{ $route.params.section_name ? $route.params.section_name.replaceAll("-", " ") : section_name }}</span>
      </b-breadcrumb-item>
      <b-breadcrumb-item :active="$route.name == 'Articles'" v-if="$route.name == 'Articles'">
        <span class="text-capitalize"> {{ $route.params.article_name ? $route.params.article_name.replaceAll("-", " ") : article_name }}</span>
      </b-breadcrumb-item>
    </b-breadcrumb>
  </div>
</template>

<script>
  import { bus } from "@/js/core/emit-bus";
  export default {
    name: "BreadCrumbs",
    props: {
      category_name: { type: String },
      section_name: { type: String },
      article_name: { type: String },
      category_id: { type: Number },
      section_id: { type: Number },
      id: { type: Number },
    },
    data() {
      return {};
    },
    methods: {
      // Navigate to Home page
      goToHome() {
        bus.$emit("close_overlay", true);
        this.$router.push({ name: "Category", query: { type: "widget" } });
      },
      // Navigate to Section page
      goToCategory() {
        bus.$emit("close_overlay", true);
        this.$router.push({
          name: "Categories",
          params: {
            category_name: this.category_name
              .trim()
              .toLowerCase()
              .replaceAll(" ", "-"),
            c_id: this.category_id,
          },
          query: { type: "widget" },
        });
      },
      // Navigate to Section page
      goToSection() {
        bus.$emit("close_overlay", true);
        this.$router.push({
          name: "Sections",
          params: {
            section_name: this.section_name
              .trim()
              .toLowerCase()
              .replaceAll(" ", "-"),
            s_id: this.section_id,
            c_id: this.category_id,
          },
          query: { type: "widget" },
        });
      },
    },
    async mounted() {},
  };
</script>
